import React from 'react';
import s from './NavBar.module.css'

function NavBar() {
    return (
        <ul className={s.NavBar}>
            <li><a href={'/PreJunior'}>PreJunior</a></li>
            <li><a href={'/Junior'}>Junior</a></li>
            <li><a href={'/JuniorPlus'}>JuniorPlus</a></li>
        </ul>
    )
};

export default NavBar;